import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import Tabs from '../../components/Tabs'

import MuiTabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Typography from '@material-ui/core/Typography'
import Box from '@material-ui/core/Box'

import { withStyles } from '@material-ui/styles'


const styles = theme => ({
  root: {
    flexGrow: 1,
    display: 'flex',
    height: 'calc(100vh - 16rem)',
    backgroundColor: theme.palette.background.paper,
    boxShadow: `0px 3px 1px -2px rgba(0,0,0,0.2),
        0px 2px 2px 0px rgba(0,0,0,0.14),
        0px 1px 5px 0px rgba(0,0,0,0.12)`,
    
    
  },
  tabs: {
    '& .MuiTabs-fixed': {
      background: theme.palette.grey['100'],
    },
    '& .MuiTabs-flexContainerVertical': {
      height: '100%',
      background: theme.palette.grey['100'],
      borderRight: `1px solid ${theme.palette.divider}`,
    },
    '& .MuiTabs-indicator': {
      left: 0,
      width: '100%',
      right: 'unset',
      background: 'transparent',
      borderLeft: `3px solid ${theme.palette.primary.main}`,
      '&::after': {
        content: '""',
        width: '100%',
        height: '100%',
        background: 'unset',
        position: 'absolute',
        right: '-2px',
        borderRight: `5px solid ${theme.palette.common.white}`,
      }
    },
    '& .Mui-selected': {
      background: theme.palette.common.white,
    }
  },
})

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
}



class DetailsWrapper extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      currentIndex: 0
    }
  }

  handleTabChange = (e, index) => {
    this.setState({
      currentIndex: index
    })
  }

  render() {
    const { currentIndex } = this.state
    const { entity, data, classes } = this.props
    const tab_items = Tabs[entity]
    
    return (
      <>
        {/* <div>This is details</div> */}
        <div className={classes.root}>
          <MuiTabs
            textColor='primary'
            value={currentIndex}
            orientation='vertical'
            indicatorColor='primary'
            className={classes.tabs}
            onChange={this.handleTabChange}

          >
            { tab_items.map(tab => <Tab label={tab.label} />) }
          </MuiTabs>

          {/* components */}
          { tab_items.map((tab, i) => (
            <TabPanel 
              index={i}
              value={currentIndex}
            >
              {tab.component({data})}
            </TabPanel>
            )
          )}
        </div>
      </>
    )
  }
}

DetailsWrapper.propTypes ={
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(DetailsWrapper)