import React, { PureComponent } from 'react'
import { connect } from 'react-redux'
import Paper from '@material-ui/core/Paper'
import Button from '@material-ui/core/Button'
import UserDialog from '../components/Dialogs/UserDialog'
import ContainerWrapper from '../App/hocs/ContainerWrapper'
import PageTableWrapper from '../App/hocs/PageTableWrapper'
import DetailsWrapper from '../App/hocs/DetailsWrapper'
import Container from '@material-ui/core/Container'
import Icon from '@material-ui/core/Icon'
import _ from 'lodash'

import { userSelector } from '../redux/selectors'

class Users extends PureComponent {
	constructor(props) {
		super(props)

		this.state = {
			isOpenDialog: false
		}

		this.columns = {
			first_name: { name: 'First Name', sortable: true, defaultActive: true},
			last_name: { name: 'Last Name', sortable: true},
			email: { name: 'Email', sortable: true},
			role: { name: 'Role', func: this.formatValue.bind(this, 'role'), sortable: true},
			status: { name: 'Status', sortable: true},
			actions: {
				name: 'Actions',
				func: this.formatValue.bind(this, 'actions')
			}
		}
	}

	formatValue(key, item) {
		if (key === 'actions') {
			return (
				<Icon
					onClick={() => this.handleView(item)}
				>remove_red_eye</Icon>
			)
		}
		if (key === 'role') {
			return <span>{_.capitalize(item[key])}</span>
		}
	}

	handleView = (item) => {
		this.props.dispatch({
			type: 'GET_USER_DETAILS_REQUESTED',
			payload: item // API: send the entire document for now
		})
	}

	handleNewDialog = (bool) => {
		this.setState({
			isOpenDialog: bool
		})
	}

	handleSortTable = (sorts) => {
		this.props.dispatch({
			type: 'USER_GRID_SORT',
			payload: sorts
		})
	}

	handleSave = (data) => {
		this.setState({
			isOpenDialog: false
		}, () => {
			console.log('dispatching..')
			// dispatch here
			// example below:
			// dispatch(CreateUser(data))
		})
	}

	render() {
		const { isOpenDialog } = this.state
		const { user: { list: { data } }, activeData } = this.props
		return (
			<Container disableGutters={true} maxWidth={false}>
				{
					activeData.type === 'parent'
					? (
						<>
								<Button
									color='primary'
									variant='contained'
									onClick={() => this.handleNewDialog(true)}
								>
									New User
								</Button>
							<Paper className='MuiPaper-tableWrapper'>
								<PageTableWrapper
									rows={data}
									columns={this.columns}
									onSortTable={this.handleSortTable}
								/>
								{
									isOpenDialog ?
									<UserDialog 
										handleSave={this.handleSave}
										handleClose={this.handleNewDialog}
									/> : null
								}
							</Paper>
						</>
					)
					: (
						<DetailsWrapper
							data={activeData}
							entity='user'
						/>
					)
				}
			</Container>
		)
	}
}

export default connect(userSelector)(ContainerWrapper(Users))