import React, { PureComponent } from 'react'
import Table from '@material-ui/core/Table'
import TableRow from '@material-ui/core/TableRow'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableContainer from '@material-ui/core/TableContainer'
import TableSortLabel from '@material-ui/core/TableSortLabel';
import TablePagination from '@material-ui/core/TablePagination'

class PageTableWrapper extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      rowsPerPage: 10,
      page: 0,
      sorts: {},
      activeSort: ''
    }
  }

  handleChangePage = (event, page) => {
    this.setState({ page })
  }

  handleChangeRowsPerPage = ({ target: { value = 10 } }) => {
    this.setState({ rowsPerPage: value })
  }

  handleSort = (key) => {
    this.setState(({ sorts }) => ({
      activeSort: key,
      sorts: {
        ...sorts,
        [key]: !sorts[key]
          ? 'asc'
          : sorts[key] === 'asc'
            ? 'desc'
            : 'asc'
      }
    }), () => this.props.onSortTable(this.state.sorts))
  }

  render() {
    const { columns, rows } = this.props
    const { rowsPerPage, page, sorts, activeSort } = this.state
    const columnsArr = Object.entries(columns)
    return (
      <>
        <TableContainer>
          <Table stickyHeader aria-label='sticky table'>
          <TableHead>
            <TableRow>
            {columnsArr.map(([key, col], i) => {
              const { name, sortable, defaultActive } = col
              const isFirst = i === 0
              const cellProps = {
                align: isFirst ? 'left' : 'center' ,
                key: `col-${i}`
              }
              return (
                <TableCell
                  {...cellProps}
                >
                  {
                    !sortable
                      ? name
                      : (
                        <TableSortLabel
                          active={key === activeSort || !!defaultActive}
                          direction={sorts[key] || 'desc'}
                          onClick={() => this.handleSort(key)}
                        >
                          {name}
                          {/* {key === activeSort ? (
                            <span>
                               sorted descending
                            </span>
                          ) : null} */}
                        </TableSortLabel>
                      )
                  }
                </TableCell>
              )
            })}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row, index) => {
              const rowProps = {
                hover: true,
                tabIndex: -1,
                key: `row-${row.id}-${index}`
              }
              return (
                <TableRow {...rowProps}>
                {columnsArr.map(([key, col], i) => {
                  const { func } = col
                  const isFirst = i === 0
                  const cellProps = {
                    key: `body-row-${i}`,
                    align: isFirst ? 'left' : 'center'
                  }
                  return (
                    <TableCell {...cellProps}>
                      {
                        func
                          ? func(row)
                          : row[key]
                      }
                    </TableCell>
                  )
                })}
                </TableRow>
              )
            })}
          </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[10, 50, 100]}
          component='div'
          count={rows.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onChangePage={this.handleChangePage}
          onChangeRowsPerPage={this.handleChangeRowsPerPage}
        />
      </>
    )
  }
}

export default PageTableWrapper