import React, { Component } from 'react'
import PropTypes from 'prop-types'

import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import Select from '@material-ui/core/Select'
import Validation from '../../validations/user'
import CloseIcon from '@material-ui/icons/Close'
import MenuItem from '@material-ui/core/MenuItem'
import TextField from '@material-ui/core/TextField'
import InputLabel from '@material-ui/core/InputLabel'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import FormControl from '@material-ui/core/FormControl'
import MuiDialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'
import FormHelperText from '@material-ui/core/FormHelperText'

import { withStyles } from '@material-ui/styles'


const styles = theme => ({
  dialogTitle: {
    margin: 0,
    padding: theme.spacing(2)
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500]
  },
  dialogActionsCancel: {
    marginRight: 'auto'
  },
  formControl: {
    marginTop: theme.spacing(1),
    minWidth: 120,
    width: '100%',
    '& .MuiInputLabel-outlined.MuiInputLabel-shrink': {
      background: theme.palette.common.white,
      paddingLeft: '5px',
      paddingRight: '5px'
    }
  }
})


class UserDialog extends Component {
  constructor(props) {
    super(props)

    this.state = {
        fieldData: {
          role: '',
          email: '',
          last_name: '',
          first_name: '',
          status: 'Active'
        },
        errors: {}
    }
  }
  
  validateInputs = () => {
    const { fieldData } = this.state
    const { isValid, errors } = Validation(fieldData)
    if (isValid) {
      this.props.handleSave(fieldData)
    } else {
      this.setState({ errors })
    }
    return isValid
  }

  handleInputChanged = (e) => {
    const { id, value } = e.target
    this.setState({
      fieldData: {
        ...this.state.fieldData,
        [id]: value
      }
    })
  }

  handleSelectChanged = (e , id) => {
    const { value } = e.target
    this.setState({
      fieldData: {
        ...this.state.fieldData,
        [id]: value
      }
    })
  }


  render() {
    const { fieldData, errors } = this.state
    const { handleClose, classes } = this.props
    const ITEM_HEIGHT = 48;
    const ITEM_PADDING_TOP = 8;
    const MenuProps = {
      PaperProps: {
        style: {
          maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
          width: 250,
        },
      },
    }

    return (
      <Dialog
        open={true}
        fullWidth
        aria-labelledby='simple-dialog-title'
      >
        <MuiDialogTitle disableTypography className={classes.dialogTitle}>
          <Typography variant='h6'>New User</Typography>
          <IconButton
            aria-label='close'
            className={classes.closeButton}
            onClick={() => handleClose(false)}
          >
            <CloseIcon />
          </IconButton>
        </MuiDialogTitle>
        <DialogContent dividers>
          <Grid container spacing={1}>
            <Grid item xs={12}>
              <Grid container spacing={1}>
                <Grid item xs>
                  <TextField
                    required
                    fullWidth
                    id='first_name'
                    label='First Name'
                    value={fieldData.first_name}
                    error={Boolean(errors && errors.first_name)}
                    helperText={errors ? errors.first_name : ''}
                    onChange={(e) => this.handleInputChanged(e)}
                    // variant='outlined'
                    // size='small'
                  />
                </Grid>
                <Grid item xs>
                  <TextField
                    required
                    fullWidth
                    id='last_name'
                    label='Last Name'
                    value={fieldData.last_name}
                    error={Boolean(errors && errors.last_name)}
                    helperText={errors ? errors.last_name : ''}
                    onChange={(e) => this.handleInputChanged(e)}
                    // variant='outlined'
                    // size='small'
                  />
                </Grid>
              </Grid>
            </Grid>
            
            <Grid item xs={12}>
              <TextField
                required
                fullWidth
                id='email'
                label='Email'
                value={fieldData.email}
                error={Boolean(errors && errors.email)}
                helperText={errors ? errors.email : ''}
                onChange={(e) => this.handleInputChanged(e)}
                // variant='outlined'
                // size='small'
              />
            </Grid>
            <Grid item xs={12}>
              <FormControl 
                required
                // variant='outlined'
                // size='small'
                className={classes.formControl}
                error={Boolean(errors && errors.role)}>
                <InputLabel>Role</InputLabel>
                <Select
                  id='role'
                  value={fieldData.role}
                  MenuProps={MenuProps}
                  onChange={(e) => this.handleSelectChanged(e, 'role')}
                >
                  <MenuItem value={'User'}>User</MenuItem>
                  <MenuItem value={'Admin'}>Admin</MenuItem>
                </Select>
                <FormHelperText>{errors ? errors.role : ''}</FormHelperText>
              </FormControl>
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button 
            color='default'
            variant='outlined'
            onClick={() => handleClose(false)}
            className={classes.dialogActionsCancel} >
            Cancel
          </Button>
          <Button onClick={() => this.validateInputs()} color='primary' variant='contained'>
            Save
          </Button>
        </DialogActions>
      </Dialog>
    )
  }
}

UserDialog.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(UserDialog)